<template>
	<view class="person_wrap">
		<template v-if="cusList.length > 0">
			<view class="personList" v-for="(item,index) in cusList" :key="index">
				<view class="listTop">
					<view class="listLeft" @click="goDet(item)">
						<view class="nameTag">
							<text class="custname">{{item.custName}}</text>
							<text class="tags" v-if="item.intensionLevelName">{{item.intensionLevelName}}</text>
							<text class="tags bule" v-if="item.stateName">{{item.stateName}}</text>
						</view>
						<view class="custMobile">{{item.custPhone}}</view>
					</view>
					<!-- <image @click="openMore(item)" src="/static/salePerson/more.png" mode=""></image> -->
					<view class="moreIcon" :style="{backgroundImage:`url(${moreIcon})`}" @click="openMore(item)"></view>
				</view>
				<view class="listBottom" @click="goDet(item)">
					<view class="bottomType">
						<text class="typeNameTag">{{item.behaviorName}}</text>
						<text class="contentText">{{item.content}}</text>
					</view>
					<text class="time">{{item.followExcuteTime}}</text>
				</view>
			</view>
		</template>
		<template v-else>
			<view class="noDataImg" :style="{backgroundImage:`url(${noDataImg})`}"></view>
		</template>
		<u-action-sheet :list="list" v-model="showMore" :cancel-btn="true" @click="clickMore"></u-action-sheet>
	</view>
</template>

<script>
	import noData from '@/static/img/noData.png';
	import moreImg from '@/static/salePerson/more.png';
	export default {
		props: ['cusList'],
		data() {
			return {
				// 更多
				list: [{
					text: '电话联系'
				}, {
					text: '分组管理'
				}],
				showMore: false,
				nowClick: {}, // 当前点击的数据
				noDataImg: noData,
				moreIcon: moreImg
			}
		},
		methods: {
			// 打开更多
			openMore(item) {
				this.showMore = true
				this.nowClick = item
			},
			// 点击更多
			clickMore(index) {
				switch (index) {
					case 0:
						uni.makePhoneCall({
							phoneNumber: this.nowClick.custPhone
						});
						break;
					case 1:
						if (getApp().globalData.is_flag) { //is_flag开始等于 true
							getApp().globalData.is_flag = false;
							uni.navigateTo({
								url:'/pages/salemanVisual/customer/setGroup?id=' + this.nowClick.id
							})
							setTimeout(() => {
								getApp().globalData.is_flag = true;
								console.log(getApp().globalData.is_flag);
							}, 1000)
						} else {}
						// uni.navigateTo({
						// 	url:'/pages/salemanVisual/customer/setGroup?id=' + this.nowClick.id
						// })
						break;
				}
			},
			// 客户详情
			goDet(item) {
				uni.navigateTo({
					url: '/pages/salemanVisual/customer/customerDet?id=' + item.id + '&state=' + item.state
				})
			},
		}
	}
</script>

<style lang="scss">
	@import '@/styles/personList.scss';
</style>
